import React,{useState,useEffect} from 'react'
import { useNavigate } from 'react-router-dom';
import { Form, Button } from 'react-bootstrap'
const Signup = () => {
    const navigate = useNavigate();
    const [name,setName] = useState("");
    const [email,setEmail] = useState("");
    const [password,setPassword] = useState("");
    const [confirmPassword,setConfirmPassword] = useState("");


    const submitHandler = (event)=>{
        event.preventDefault();
        if(password === confirmPassword){
            navigate('/login')
        }else{
            alert("Password do not match! Please try again");
        }
    }

    useEffect(()=>{

    },[])


    return (
        <div className="d-flex justify-content-center align-items-center vh-100 bh-light" style={{ minHeight: '100vh' }}>
            <Form className="card shadow p-5" onSubmit={(event)=>{submitHandler(event)}} >
                <h2 className='text-center'>Register</h2>
                <Form.Group className="mb-3 " controlId="formBasicEmail" >
                    <Form.Label>Name</Form.Label>
                    <Form.Control type="text" placeholder="Full Name" required={true} value={name} onChange = {(event)=>{setName(event.target.value)}} />
                </Form.Group>

                <Form.Group className="mb-3 " controlId="formBasicEmail" >
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" placeholder="Enter email" required={true} vlaue = {email} onChange = {(event)=>{setEmail(event.target.value)}} />
                    <Form.Text className="text-muted">
                        We'll never share your email with anyone else.
                    </Form.Text>
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" placeholder="Password" required={true} value ={password} onChange = {(event)=>{setPassword(event.target.value)}} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                    <Form.Label>Confirm Password</Form.Label>
                    <Form.Control type="password" placeholder="Confirm Password" required={true} value = {confirmPassword} onChange = {(event)=>{setConfirmPassword(event.target.value)}} />
                </Form.Group>

                <div className="text-center">
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </div>
            </Form>
        </div>
    )
}

export default Signup