import { React } from 'react'
import { Link } from 'react-router-dom'
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { AiOutlineShoppingCart } from "react-icons/ai";
import SerachBox from '../Search/Search'

import {useSelector,useDispatch} from 'react-redux';
import {logout} from '../../redux/slices/userSlice'
const NavbarComp = () => {
  
  const items = useSelector((state)=>state.cart);

  const loggedStatus = useSelector((state) => state.user);


  const dispatch = useDispatch();

  return (
    <>
      <Navbar collapseOnSelect expand="lg" className="bg-dark navbar-dark py-3 sticky-top">
        <Container>
          <Navbar.Brand as={Link} to="/">LearnX</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="">
              <Nav.Link as={Link} to="/" eventkey="1">Home</Nav.Link>
            </Nav>
            <Nav className="ms-auto">
              <SerachBox />
            </Nav>

            <Nav className="ms-auto">
              <Link to="/cart" eventkey="2" className="btn btn-primary position-relative me-4">
                <AiOutlineShoppingCart />
                <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">{items.length}</span>
              </Link>
              {
                loggedStatus ?
                  <>
                    <NavDropdown title="Profile" className="btn btn-primary p-0" id="basic-nav-dropdown">
                      <NavDropdown.Item as={Link} to="/profile" eventKey="3" >Profile</NavDropdown.Item>
                      <NavDropdown.Item onClick={(event)=>{dispatch(logout({type:'USER'}))}} eventkey="4" >Logout</NavDropdown.Item>
                    </NavDropdown>
                  </>
                  :
                  <>
                    <Link to="/register" eventkey="5" className="btn btn-primary me-2">Register</Link>
                    <Link to="/login" eventkey="6" className="btn btn-primary">Login</Link>
                  </>
              }
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  )
}

export default NavbarComp