import React from 'react'
import { Button, Card } from 'react-bootstrap';
import { useNavigate, Link } from 'react-router-dom';
import {useDispatch} from 'react-redux';
import {addItems} from '../../redux/slices/cartSlices'

import CoursesData from '../../data.json'

const Courses = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const addToCartHandler = (event,item) =>{
        dispatch(addItems({id:item.id,name:item.productName,price:item.productPrice,thumbnail:item.productImage}));
        navigate('/cart');
    }


    return (
        <div className="container-fluid">
            <div className="row justify-content-center">
                <h2 className="text-center mt-4 fs-1">Courses for you...</h2>
                {
                    CoursesData.map((item, index) => {
                        return (
                            <div className="col-lg-3 col-md-6 col-12 p-4" key={index}>
                                <Card >
                                    <Card.Img variant="top" src={item.productImage} />
                                    <Card.Body>
                                        <Card.Title className="text-center fs-4 fw-bold text-decoration-none" as={Link} to={`learn/${item.productName}/${item.id}`} >{item.productName}</Card.Title>
                                        <Card.Text className="m-0">
                                            <span className="fw-bold">Category-</span> {item.productCategory}
                                        </Card.Text>
                                        <Card.Text>
                                        <span className="fw-bold">&#8377;</span> <span>{item.productPrice}/-</span> 
                                        </Card.Text>
                                        <Button variant="primary"onClick={(event)=>{addToCartHandler(event,item)}} >Add to Cart</Button>
                                    </Card.Body>
                                </Card>
                            </div>
                        )
                    })
                }

            </div>
        </div>
    )
}

export default Courses