import React, { useState } from 'react'
import { Button, Modal } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { removeItems } from '../redux/slices/cartSlices'


const Cart = () => {
  const items = useSelector((state) => state.cart);
  const dispatch = useDispatch();

  const navigate = useNavigate();

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // payment details 
  const [cardNumber, setCardNumber] = useState('');
  const [cardName, setCardName] = useState('');
  const [expiryDate, setExpiryDate] = useState('');
  const [cvv, setCvv] = useState('');

  const handleCardNumberChange = (e) => {
    setCardNumber(e.target.value);
  };

  const handleCardNameChange = (e) => {
    setCardName(e.target.value);
  };

  const handleExpiryDateChange = (e) => {
    setExpiryDate(e.target.value);
  };

  const handleCvvChange = (e) => {
    setCvv(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Process payment or submit form data here
    alert('Payment submitted!');
  };



  return (
    <div className="container">
      <div className="row justify-content-center align-items-center">
        {
          items.length > 0 ?
            < >
              <div className="col-md-7 col-12 py-md-5 py-3">
                {
                  items.map((item, index) => {
                    return (
                      items.length > 0 &&
                      <div className="card mb-3" key={index}>
                        <div className="row g-0">
                          <div className="col-md-4">
                            <img src={item.thumbnail} className="img-fluid rounded-start" alt="product_image" />
                          </div>
                          <div className="col-md-8">
                            <div className="card-body">
                              <h5 className="card-title">{item.name}</h5>
                              <p className="card-text">
                                <span className="fw-bold">&#8377;</span> <span>{item.price}/-</span>
                              </p>
                              <Button variant="primary" onClick={(event) => { dispatch(removeItems({ id: item.id })) }} >Remove Item</Button>
                            </div>
                          </div>
                        </div>
                      </div>
                    )
                  })
                }
              </div>
              <div className="col-md-5 col-12 text-center">
                <h4>Total: <span>&#8377;</span>{items.reduce((total, item) => { return total + item.price }, 0)}</h4>
                <Button variant="success" onClick={handleShow}>Proceed to Pay</Button>
              </div>
            </>
            :
            <div className="d-flex justify-content-center mt-md-5">
              <Button onClick={()=>{navigate('/')}}>Add Items to Cart</Button>
            </div>
        }
      </div>
      {/* Modal to make payment */}

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Payment Form</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form onSubmit={handleSubmit}>
            <div className="mb-3">
              <label htmlFor="cardNumber" className="form-label">
                Card Number
              </label>
              <input
                type="text"
                className="form-control"
                id="cardNumber"
                value={cardNumber}
                onChange={handleCardNumberChange}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="cardName" className="form-label">
                Cardholder Name
              </label>
              <input
                type="text"
                className="form-control"
                id="cardName"
                value={cardName}
                onChange={handleCardNameChange}
              />
            </div>
            <div className="row">
              <div className="col-md-6 mb-3">
                <label htmlFor="expiryDate" className="form-label">
                  Expiry Date
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="expiryDate"
                  value={expiryDate}
                  onChange={handleExpiryDateChange}
                />
              </div>
              <div className="col-md-6 mb-3">
                <label htmlFor="cvv" className="form-label">
                  CVV
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="cvv"
                  value={cvv}
                  onChange={handleCvvChange}
                />
              </div>
            </div>
            
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <button type="submit" className="btn btn-primary">
              Pay
            </button>
        </Modal.Footer>
      </Modal>






    </div>




  )
}

export default Cart