import {createSlice} from '@reduxjs/toolkit';

const cartSlice = createSlice({
    name:'Cart',
    initialState: [],
    reducers:{
        addItems: (state,action)=>{
            const existingItems = JSON.parse(JSON.stringify(state));
            let index = existingItems.findIndex(item=>item.id === action.payload.id);
            if(index !== -1){
                alert("Item added to cart already, go to cart!");
            }else{
                state.push(action.payload);
                alert("Item added to cart, go to cart!");
            }
        },
        removeItems: (state,action)=>{
            let existingItems = JSON.parse(JSON.stringify(state));
            while(state.length > 0){
                state.pop();
            }
            state.push(...existingItems.filter((item)=>item.id !== action.payload.id));
        }
    }
});

export const {addItems} = cartSlice.actions
export const {removeItems} = cartSlice.actions

export default cartSlice.reducer;