import React,{useState,useEffect} from 'react'
import { Form, Button } from 'react-bootstrap';
import { useDispatch,useSelector } from 'react-redux';
import {adminLogin} from '../../redux/slices/adminSlice'
import { useNavigate } from 'react-router-dom';


const Login = () => {
  let loggedStatus = useSelector((state)=>state.admin);
  const navigate = useNavigate();
  const [username,setUsername] = useState("");
  const [password,setPassword] = useState("");

  const dipatch = useDispatch();

  const submitHandeler = (event)=>{
    event.preventDefault();
    dipatch(adminLogin({username:username,password:password}));
  }


  useEffect(()=>{
    if(loggedStatus){
      navigate('/admin/dashboard');
    }
  },[loggedStatus,navigate]);

  return (
    <div className="container vh-100 d-flex justify-content-center align-items-center">
      <Form className="p-5 card shadow" onSubmit={(event)=>{submitHandeler(event)}}>
      <h2 className="text-center text-primary mb-3">LeranX Adminstration</h2>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Email address</Form.Label>
          <Form.Control type="email" placeholder="Enter email" required={true} value={username} onChange={(event)=>{setUsername(event.target.value)}} />
          <Form.Text className="text-muted">
            We'll never share your email with anyone else.
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control type="password" placeholder="Password" required={true} value={password} onChange={(event)=>{setPassword(event.target.value)}} />
        </Form.Group>
        
        <Button variant="primary" type="submit">
          Submit
        </Button>
      </Form>
    </div>
  )
}

export default Login