import React, { useEffect, useState } from 'react'
import { Button, Card, Modal, Form } from 'react-bootstrap';


import { useNavigate, Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { adminLogout } from '../../redux/slices/adminSlice';
import { deleteProducts } from '../../redux/slices/productSlices'

const Dashboard = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const loggedStatus = useSelector((state) => state.admin);
  const courseList = useSelector((state) => state.product);

  useEffect(() => {
    if (!loggedStatus) {
      navigate('/admin');
    }
  }, [loggedStatus, navigate]);

  const sideNavs = [
    {
      name: "Dashboard",
      path: '/admin/dashboard',
    },
    {
      name: "Add New Course",
      path: '/admin/addCourse',
    },
  ]

  const [show, setShow] = useState(false);

  const [thumbnail, setThumbnail] = useState("");
  const [courseName, setCourseName] = useState("");
  const [courseCategory, setCourseCategory] = useState("");
  const [coursePrice, setCoursePrice] = useState(0);
  const [courseid,setCourseid] = useState('');



  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const getCourseToEdit = (id) => { 
    let courseToEdit = courseList.filter(c => c.id === id);
    setThumbnail(courseToEdit[0].productImage);
    setCourseName(courseToEdit[0].productName);
    setCourseCategory(courseToEdit[0].category);
    setCoursePrice(courseToEdit[0].productPrice);
    setCourseid(courseToEdit[0].id);
  }

  console.log(thumbnail);

  const initializer = () => {
    setThumbnail("");
    setCourseName("");
    setCourseCategory("");
    setCoursePrice(0);
    setCourseid('');
  }

  const submitHandeler = (event) => {
    event.preventDefault();
    
    initializer();
  }

  return (
    <div className='container-fluid bg-dark vh-100 '>
      <div className="row justify-content-start bg-light vh-100" sytle={{ minHeight: '100vh' }} >
        <div className="col-md-3 border-2 border-end">
          <h2 className="text-center py-2">LearnX</h2>
          <hr />
          <ul className="list-unstyled ms-4">
            {
              sideNavs.map((sideNav, index) => (
                <li className='py-2' key={index}> <Link to={sideNav.path} className="text-decoration-none fs-4" >{sideNav.name}</Link></li>
              ))
            }
          </ul>
          <div className="text-start ms-4">
            <Button variant="danger" onClick={(event) => { dispatch(adminLogout()) }} >Logout</Button>
          </div>
        </div>
        <div className="col-md-9">
          <h2 className="text-center py-2">Wellcome to LearnX Administration</h2>
          <hr />
          <div className="row justify-content-center">
            {
              courseList.map((course, index) => (
                <div className="col-md-4 p-4" key={index}>
                  <Card >
                    <Card.Img variant="top" src={course.productImage} />
                    <Card.Body>
                      <Card.Title className="text-center" >{course.productName}</Card.Title>
                      <Card.Text className="m-0">
                        <span className="fw-bold">Category-</span> {course.productCategory}
                      </Card.Text>
                      <Card.Text>
                        <span className="fw-bold">&#8377;</span> <span>{course.productPrice}/-</span>
                      </Card.Text>
                      <div className="d-flex justify-content-around">
                        <Button variant="primary " onClick={() => { handleShow(); getCourseToEdit(course.id) }}>Update</Button>
                        <Button variant="danger" onClick={() => { dispatch(deleteProducts({ id: course.id })) }}>Delete</Button>
                      </div>
                    </Card.Body>
                  </Card>
                </div>
              ))
            }
          </div>
        </div>
      </div>
      {/* Modal to update course details */}

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Modal title</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form className="p-5 border rounded" onSubmit={(event) => { submitHandeler(event) }}>

            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Course Thumbnail</Form.Label>
              <Form.Control type="text" placeholder="Thumbnail URL" required={true} value={thumbnail} onChange={(event) => { setThumbnail(event.target.value) }} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Course Name</Form.Label>
              <Form.Control type="text" placeholder="ReactJS" required={true} value={courseName} onChange={(event) => { setCourseName(event.target.value) }} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Course Category</Form.Label>
              <Form.Control type="text" placeholder="Web Development..." required={true} value={courseCategory} onChange={(event) => { setCourseCategory(event.target.value) }} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Price(&#8377;)</Form.Label>
              <Form.Control type="text" placeholder="1000" required={true} value={coursePrice} onChange={(event) => { setCoursePrice(Number(event.target.value)) }} />
            </Form.Group>
            <Button variant="primary" type="submit">Add Course</Button>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary">Understood</Button>
        </Modal.Footer>
      </Modal>





    </div>
  )
}

export default Dashboard