import {createSlice} from '@reduxjs/toolkit';
import productData from '../../data.json'


const productSlice = createSlice({
    name:"products",
    initialState:productData,
    reducers:{
        addProducts:(state,action)=>{
            // new product will be added here
            state.push({id:`${state.length+1}`, productName:action.payload.productName,productCategory:action.payload.productCategory,productPrice:action.payload.productPrice,productImage:action.payload.productImage});
        },
        deleteProducts:(state,action)=>{
            // deleting products
            let existingItems = JSON.parse(JSON.stringify(state));
            console.log(existingItems);
            while(state.length > 0){
                state.pop();
            }
            state.push(...existingItems.filter((item)=>item.id !== action.payload.id));
        },
        // updateProducts:(state,action)=>{
        //     // updating products
        // }
    }
});

export const {addProducts} = productSlice.actions
export const {deleteProducts} = productSlice.actions
// export const {updateProducts} = productSlice.actions

export default productSlice.reducer;