import React from 'react'
import {Form,Row,Col} from 'react-bootstrap';
const Search = () => {
  return (
    <>
      <Form  className="">
        <Row>
          <Col xs="auto">
            <Form.Control
              type="text"
              placeholder="Search"
              className=" mr-sm-2"
            />
          </Col>
        </Row>
      </Form> 
    </>
  )
}

export default Search