import React, { useState,useEffect } from 'react'
import { Button, Form } from 'react-bootstrap';
import { useNavigate, Link } from 'react-router-dom';

import { useSelector, useDispatch } from 'react-redux';
import { adminLogout } from '../../redux/slices/adminSlice';
import {addProducts} from '../../redux/slices/productSlices'

const AddCourse = () => {
    const [thumbnail, setThumbnail] = useState("");
    const [courseName,setCourseName] = useState("");
    const [courseCategory,setCourseCategory] = useState("");
    const [coursePrice,setCoursePrice] = useState(0);


    const dispatch = useDispatch();
    const navigate = useNavigate();
    const loggedStatus = useSelector((state) => state.admin);


    const sideNavs = [
        {
            name: "Dashboard",
            path: '/admin/dashboard',
        },
        {
            name: "Add New Course",
            path: '/admin/addCourse',
        },
    ]

    const initializer = () => {
        setThumbnail("");
        setCourseName("");
        setCourseCategory("");
        setCoursePrice(0);
    }

    const submitHandeler = (event)=>{
        event.preventDefault();
        // alert(`${thumbnail},${courseName},${courseCategory},${coursePrice}`);
        dispatch(addProducts({productName:courseName,productCategory:courseCategory,productPrice:coursePrice,productImage:thumbnail}));
        initializer();
    }



    useEffect(() => {
        if (!loggedStatus) {
            navigate('/admin');
        }
    }, [loggedStatus, navigate]);


    return (
        <div className='container-fluid vh-100 '>
            <div className="row justify-content-start vh-100 bg-light" sytle={{ minHeight: '100vh' }} >
                <div className="col-md-3 border-2 border-end">
                    <h2 className="text-center py-2">LearnX</h2>
                    <hr /> 
                    <ul className="list-unstyled ms-4">
                        {
                            sideNavs.map((sideNav,index) => (
                                <li className='py-2' key={index}> <Link to={sideNav.path} className="text-decoration-none fs-4" >{sideNav.name}</Link></li>
                            ))
                        }
                    </ul>
                    <div className="text-start ms-4">
                        <Button variant="danger" onClick={(event) => { dispatch(adminLogout()) }} >Logout</Button>
                    </div>
                </div>
                <div className="col-md-9">
                    <h2 className="text-center py-2">Add Course</h2>
                    <hr />
                    <Form className="p-5 border rounded" onSubmit={(event)=>{submitHandeler(event)}}>

                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Course Thumbnail</Form.Label>
                            <Form.Control type="text" placeholder="Thumbnail URL" required={true} value={thumbnail} onChange = {(event)=>{setThumbnail(event.target.value)}} />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Course Name</Form.Label>
                            <Form.Control type="text" placeholder="ReactJS" required={true} value={courseName} onChange = {(event)=>{setCourseName(event.target.value)}} />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Course Category</Form.Label>
                            <Form.Control type="text" placeholder="Web Development..." required={true} value ={courseCategory} onChange={(event) => {setCourseCategory(event.target.value)}} />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Price(&#8377;)</Form.Label>
                            <Form.Control type="text" placeholder="1000" required={true} value= {coursePrice} onChange ={(event) => {setCoursePrice(Number(event.target.value))}} />
                        </Form.Group>
                        <Button variant="primary" type="submit">Add Course</Button>
                    </Form>
                </div>
            </div>
        </div>
    )
}

export default AddCourse