import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Form, Button } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import {login} from '../../redux/slices/userSlice'

const Login = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const submitHandler = (event) => {
        event.preventDefault();
        dispatch(login({username:username,password:password}));
        setUsername("");
        setPassword("");
        navigate('/');
    }
    


    return (
        <div className="container-fluid bg-light vh-100 d-flex justify-content-center align-items-center">
            <Form className="border p-4 card shadow" onSubmit={(event) => { submitHandler(event) }}>
                <h2 className='text-center'>Login</h2>
                <Form.Group className="mb-3 " controlId="formBasicEmail" >
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" placeholder="Enter email" required= {true} value={username} onChange={(event) => { setUsername(event.target.value) }} />
                    <Form.Text className="text-muted">
                        We'll never share your email with anyone else.
                    </Form.Text>
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" placeholder="Password" required = {true} value={password} onChange={(event) => { setPassword(event.target.value) }} />
                </Form.Group>

                <div className="text-center">
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </div>
            </Form>
        </div>
    )
}

export default Login