import React from 'react'
import { useParams } from 'react-router-dom';
import { useSelector } from 'react-redux'

const Learn = () => {
    const params = useParams();
    const courseId = params.CourseId;
    const courseList = useSelector((state) => state.product);
    const courseDetails = courseList.filter(c => c.id === courseId);
    

    return (
        <div className='container-fluid bg-light vh-100'>
            <h2 className='text-center pt-4' >Video Page</h2>
            <div className="container d-flex justify-content-center mt-5">
            <iframe width="560" height="315" src="https://www.youtube.com/embed/MAKWhN32T6k" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            </div>
            <div className="container text-center mt-5">
                <p>
                    {courseDetails[0].content}
                </p>
            </div>
        </div>
    )
}

export default Learn